      border-radius:50%
      border:1px solid red
    .appoint_box
        width:74px
        height:74px
        position:absolute
        border-radius:50%
        border-sizing:content-box
        background:#0075BF
        top:0
        bottom:0
        left:-36px
        margin:auto
        z-index:999
        color:#fff
        text-align:center
       .iconfont
         font-size:0.48rem
         display:block
         padding-top:10px
         transition:all 1s ease
         animation:spin 1.5s linear infinite
       p
         height:0.3rem
         line-height:0.3rem
         font-size:0.26rem
         animation:swinging  2s ease-in-out 3s 1 alternate both
    .pulse
        width:92px
        position: absolute
        top:0
        bottom:0
        left:-51px
        margin:auto
        border-sizing:content-box
        height:92px
        border-radius:50%
        border: 6px solid #0893EA
        opacity: 0 
        animation: halo 2s ease-out infinite 
    .pulse_1
        width:78px
        position: absolute
        top:0
        bottom:0
        border-sizing:content-box
        margin:auto
        left:-39px
        height:78px
        border-radius:50%
        opacity:1
        animation:scales 1.5s ease-out infinite
        border: 1px solid rgba(8,147,234.0.6)
    .pulse_2
        width:70px
        position: absolute
        border-sizing:content-box
        top:0
        bottom:0
        left:-35px
        margin:auto
        height:70px
        border-radius:50%
        animation:scales 2s ease-out infinite
        border:1px solid rgba(8,147,234.0.4)
      
  .swiper-slide
    box-shadow: 0px 7px  5px rgba(231,227,227,1)
    height:2.8rem
    img
       min-height:2.8rem
       max-height:100%
       width:100%      
.content
  width:95%
  margin:0 auto
.part_wrap
  padding-bottom:0.3rem
  background:rgba(244,246,246,0.48)
 .part_box
      background:#fff
.toggleMore
    p
     padding:5px 0
     font-size:0.26rem
     color:#666
.title 
     height:0.7rem
     line-height:0.8rem
     margin:0 auto
     p
      font-size:0.28rem
      color:#333
     span
        float:right
        font-size:0.23rem
        color:#999
        .iconfont
         font-size:0.24rem
         margin-right:2px
        .icon-youhua
          font-size:0.2rem      
.title_1
          span
            color:#333
            span
              display:inline-block
              margin-left:4px
            .iconfont
               color:#E92528
            .icon-xiaolaba
               color:red
               font-size:0.28rem
               animation:pulse 1s linear infinite
.address
    padding:20px 0 20px 0
    h2
       font-weight:normal 
       font-size:0.3rem 
       span
         font-size:0.25rem
         display:inline-block
         margin-left:4px
         color:#999
    p:nth-child(2)
        margin-top:5px
        color:#333
        span
          display:inline-block
          margin-right:5px
          color:red
          .iconfont
             margin-right:5px
             font-size:0.23rem
    p:nth-child(3)
        .iconfont
          font-size:0.18rem
          color:red
        span
          display:inline-block
          padding-left:2px
    p:nth-child(4)
          padding:5px 0
          .scale-1px:after
             border-radius:10px
          span
              display:inline-block
              margin-right:3px
              margin-bottom:8px
              padding:0 4px
              z-index:2        
    p
       color:#999
       font-size:0.24rem
       height:0.4rem
       line-height:0.4rem   
.part
  display:flex
  padding-bottom:10px
  font-size:0.25rem
  div:nth-child(1)
    color:black
    width:18%
    font-size:0.27rem
    text-align:left
  div:nth-child(2)
    width:82%
    color:#555
    span
      color:#20A2DD
      .iconfont
        margin-left:5px
        font-size:0.24rem
    p
      padding-bottom:10px
.hospital_pic
			margin:0 0 12px 0
			letter-spacing:-8px
			//border:1px solid red
			li:nth-child(3n)
				margin-right:0
			li
				width:31%
				height:1.2rem
				margin-bottom:5px
				margin-right:3%
				letter-spacing:normal
				display:inline-block
				overflow:hidden
				position:relative
				p
					height:0.28rem
					line-height:0.28rem
					width:100%
					text-align:left
					color:#fff
					font-size:0.22rem
					position:absolute
					text-indent:0.1rem
					bottom:0
					background:rgba(0,0,0,0.4)
				img
					width:auto
					height:100%					
.part_4
          margin:0 auto
          width:100%
          display:flex
          flex-wrap:wrap
          padding-top:15px
          li
             width:25%
             text-align:center
             box-sizing:border-box
             margin-bottom:10px
             //border:1px solid red 			
.active 
          li:nth-child(1) .iconfont
              color: #F16B5C
          li:nth-child(2) .iconfont
              color: #E82427
          li:nth-child(3) .iconfont
              color:#94cdac
          li:nth-child(4) .iconfont
              color:#FAB842 
          li:nth-child(5) .iconfont
              color:#F8BF36 
          li
            .iconfont
               font-size:0.78rem  
            p
               font-size:0.25rem
               height:0.4rem
               line-height:0.4rem
               color:#333        
.doctor
        .img
          padding:0 8px
          height:1.2rem
          box-sizing:border-box
          overflow:hidden
          img
            width:auto
            height:100%
        p
          heigth:0.3rem
          line-height:0.3rem
          width:100%
          text-align:center
          //text-indent:8px
          span
            display:inline-block
        p:nth-child(2)
          padding-top:7px
          padding-bottom:3px
          color:#333
          font-size:0.25rem
          span
            .iconfont
              color:#F4E336
              font-size:0.23rem
              margin-left:3px
        p:nth-child(3)
          font-size:0.22rem
          ///width:94%
          margin:auto
          text-align:center
          .scale-1px:after
               border-radius:10px
               //border:1px solid #F58D34 
          span
              color:#777
              height:0.3rem
              margin:auto
              display:inline-block
              width:1rem
              box-sizing:border-box
              line-height:0.3rem
              padding:0 5px 
              z-index:2
        p:nth-child(4)
          color:#999
          font-size:0.21rem
 @keyframes scales
    0%   
        transform: scale(1)  
        opacity: 1
    
    25% 
        transform: scale(1.1) 
        opacity: 0.8
     
    50% 
        transform: scale(1.12)
        opacity: 0.5
    
    75% 
        transform: scale(1.14) 
        opacity: 0.5
  
    100% 
        transform: scale(0) 
        opacity: 0  
        

 @keyframes swinging
   0% 
     transform: rotate(0deg)
   25%
     transform: rotate(-10deg)
   50%
     transform: rotate(0deg)
   75%
     transform: rotate(10deg)
   100%
     transform: rotate(0deg)

 
  @keyframes spin
    0%   
        transform:scale(0.9) 
    
    50% 
        transform:rotate(1.1) 
     
    100% 
        transform:rotate(0.9)
    
 @keyframes halo 
    0%   
        transform: scale(0)  
        opacity: 0.0
    
    25% 
        transform: scale(0) 
        opacity: 0.1
     
    50% 
        transform: scale(0.1)
        opacity: 0.3
    
    75% 
        transform: scale(0.5) 
        opacity: 0.5
  
    100% 
        transform: scale(1) 
        opacity: 0.0 




 
</style>




